import React,{Component} from "react";
import Row from "react-bootstrap/Row";

import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/Dropdown";
import {Route} from "react-router-dom";
import Col from "react-bootstrap/Col";
import SimpleArticle from "../templates/SimpleArticle";
import {createArticle, updateArticle} from "../actions/article";
import connect from "react-redux/es/connect/connect";


class CreateArticlePage extends Component{
    constructor(props){
        super();

        this.getArticleLayoutData.bind(this);

        if(props.location.state !== undefined && props.location.state.edit){
            this.state = ({
                selectedLayout: props.location.state.article.template,
                paragraphs: 0,
                preview: false,
                title: props.location.state.article.title,
                date: new Date(props.location.state.article.date).toISOString(),
                author: props.location.state.article.author,
                lead: props.location.state.article.lead,
                sources: props.location.state.article.sources,
                tags: props.location.state.article.tags,
                template: props.location.state.article.template,
                layoutData: this.getArticleLayoutData(props.location.state.article),
                publish:props.location.state.article.published,
            });
        } else {
            this.state = ({
                selectedLayout: "Layout template",
                paragraphs: 0,
                preview: false,
                title: "",
                date: new Date().toISOString(),
                author: "",
                lead: "",
                sources: "",
                tags: "",
                template: "",
                layoutData: null,
                publish:false
            });
        }


        this.titleNode = React.createRef();
        this.dateNode = React.createRef();
        this.authorNode = React.createRef();
        this.leadNode = React.createRef();
        this.sourcesNode = React.createRef();
        this.tagsNode = React.createRef();
        this.publishNode = React.createRef();

        this.renderLayout.bind(this);
        this.saveArticle.bind(this);
        this.setLayoutData.bind(this);

        this.article = {
            title:this.state.title,
            date:this.state.date,
            author:this.state.author,
            lead:this.state.lead,
            sources:this.state.sources,
            tags:this.state.tags,
            template:this.state.template,
        }
    }

    getArticleLayoutData(article){
        switch (article.template) {
            case "Simple":
                return {text:article.text, img_url:article.img_url}
            default:
                return {text:"", img_url:""}
        }
    }

    setLayoutData(data){
        console.log("setting layout data");
        this.setState({
            layoutData: data,
        })
    }

    saveArticle(){

        if(!this.props.location.state.edit) {
            let newArticle = {
                title:this.state.title,
                date:this.state.date,
                author:this.state.author,
                lead:this.state.lead,
                sources:this.state.sources,
                tags:this.state.tags,
                template:this.state.template,
                layoutData: this.state.layoutData,
                published: this.publishNode.checked,
            };
            this.props.createArticle(newArticle);
        } else {
            let newArticle = {
                id: this.props.location.state.article._id,
                title:this.state.title === "" ? this.props.location.state.article.title : this.state.title,
                date:this.state.date=== "" ? this.props.location.state.article.date: this.state.date,
                author:this.state.author === "" ? this.props.location.state.article.author : this.state.author,
                lead:this.state.lead === "" ? this.props.location.state.article.lead : this.state.lead,
                sources:this.state.sources === "" ? this.props.location.state.article.sources : this.state.sources,
                tags:this.state.tags === "" ? this.props.location.state.article.tags : this.state.tags,
                template:this.state.template === "" ? this.props.location.state.article.template : this.state.template,
                published: this.publishNode.checked,
                ...this.state.layoutData,
            };
            this.props.updateArticle(newArticle);
        }
    }

    renderLayout(event){
        this.setState({
            selectedLayout:event,
            template:event,
        });
    }

    render(){
        return(
            <div style={{padding:"1rem"}}>
                <h2>Create new article</h2>
                <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="label">Article Title:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        placeholder="title"
                        aria-label="title"
                        aria-describedby="label"
                        ref = {this.titleNode}
                        defaultValue = {this.state.title}
                        onChange = {(event) => this.setState({title:event.target.value})}
                    />
                </InputGroup>

                <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="label">Date:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        type="date"
                        placeholder="date"
                        aria-label="date"
                        aria-describedby="date"
                        ref = {this.dateNode}
                        defaultValue={new Date(this.state.date).toISOString().split("T")[0]}
                        //value =
                        onChange = {(event) => this.setState({date:event.target.value})}
                    />
                </InputGroup>
                <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="label">Author:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        placeholder="author name"
                        aria-label="Author"
                        aria-describedby="label"
                        defaultValue = {this.state.author}
                        onChange = {(event) => this.setState({author:event.target.value})}
                    />
                </InputGroup>

                <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                      <InputGroup.Text>Lead:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        as="textarea"
                        placeholder="lead text"
                        aria-label="Lead"
                        ref = {this.leadNode}
                        defaultValue = {this.state.lead}
                        onChange = {(event) => this.setState({lead:event.target.value})}
                    />
                </InputGroup>

                <InputGroup className="mb-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="label">Sources:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        placeholder="sources"
                        aria-label="sources"
                        aria-describedby="label"
                        defaultValue = {this.state.sources}
                        onChange = {(event) => this.setState({sources:event.target.value})}
                    />
                </InputGroup>

                <InputGroup className="mt-3">
                    <InputGroup.Prepend>
                        <InputGroup.Text id="label">Tags:</InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        placeholder="tags separated by comma"
                        aria-label="tags"
                        aria-describedby="tags"
                        ref = {this.tagsNode}
                        defaultValue = {this.state.tags}
                        onChange = {(event) => this.setState({tags:event.target.value})}
                    />
                </InputGroup>

                <InputGroup className="mt-3 mb-3">
                    <DropdownButton
                        as={InputGroup.Prepend}
                        variant="outline-secondary"
                        title={this.state.selectedLayout}
                        id="input-group-dropdown-1"
                        onSelect={(event) => {
                            this.renderLayout(event);
                        }}
                    >
                      <Dropdown.Item eventKey="Simple">Simple</Dropdown.Item>
                      <Dropdown.Item eventKey="Basic">Basic</Dropdown.Item>
                      <Dropdown.Item eventKey="Custom">Complex</Dropdown.Item>
                    </DropdownButton>

                </InputGroup>

                {this.state.selectedLayout !== "Layout template" &&
                    <h3>{this.state.selectedLayout} Article Layout</h3>
                }

            {this.state.selectedLayout === "Simple" &&
                <div mt={2}>
                <SimpleArticle
                    editArticle={true}
                    showArticle={this.state.preview}

                    title={this.state.title}
                    author={this.state.author}
                    lead={this.state.lead}
                    sources={this.state.sources}
                    tags={this.state.tags}
                    date={this.state.date}
                    layoutData={this.state.layoutData == null ? {text:"", img_url:""} : this.state.layoutData}
                    layoutDataCallback={(data) => this.setLayoutData(data)}
                />

                <Button style={{marginTop:"2rem"}} size="sm" onClick={() => this.setState({preview:!this.state.preview})}>

                    {this.state.preview &&
                    <a>Hide preview</a>
                    }
                    {!this.state.preview &&
                    <a>Show preview</a>
                    }

                </Button>

                </div>
            }

            <Container style={{marginTop:"2rem"}}>
            <Row>
                <Col md={2} m={1}>
                    <input ref={ref => this.publishNode = ref}
                        defaultChecked = {this.state.publish}
                        className="mr-2" id="publish" type="checkbox" onChange={(event) => {
                            this.setState({publish:event.target.checked});
                    }}/>
                    <label htmlFor="publish">Publish</label>
                </Col>
                <Col md={2}>
                    <Route render={({history}) => (
                        <Button className="m-1" size="sm" onClick={() => {
                            this.saveArticle();
                            history.push('/management');
                        }} >Save</Button>
                    )}/>
                </Col>
                <Col md={2}>
                    <Route render={({history}) => (
                        <Button className="m-1" size="sm" onClick={() => history.push('/management')} >Cancel</Button>
                    )}/>
                </Col>
            </Row>
            </Container>
            </div>
        );
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const auth = state.AuthReducer;
    return {
        articles: article.articles,
        isAuthenticated: auth.isAuthenticated
    }
}

export default connect(mapStateToProps,{
    createArticle,
    updateArticle
})(CreateArticlePage);