import {Component} from "react";
import Container from "react-bootstrap/Container";
import React from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import {loginUser, logoutUser} from "../actions/authentication";
import connect from "react-redux/es/connect/connect";
import {getArticles, gotoArticle, setArticleFilter} from '../actions/article';
import {
    Link
} from "react-router-dom";
import {FacebookShareButton, TwitterShareButton, FacebookIcon, TwitterIcon} from 'react-share';
import {Route} from "react-router-dom";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Spinner from "react-bootstrap/Spinner";
import {months} from "../utils";
import Figure from "react-bootstrap/Figure";

class MainPage extends Component {

    constructor(){
        super();

        this.mainFigureNode = React.createRef();
    }

    componentDidMount(){
        if(this.props.articles.length===0) {
            this.props.getArticles();
        }
    }

    renderArticle(article){
        this.props.gotoArticle(article)
    }

    render(){
        return (
            <Container style={{margin: this.props.isMobileDevice ? 0 : 'auto', padding: this.props.isMobileDevice ? 0 : 'auto'}}>

                {this.props.loading &&
                    <div className="text-center m-5">
                        <Spinner animation="border" variant="success"/>
                    </div>
                }

                {this.props.articles.map((article,index) => (
                    <div key={index}>
                        {(this.props.filter === "" || article.title.toLowerCase().includes(this.props.filter.toLowerCase()) || article.tags.toLowerCase().includes(this.props.filter.toLowerCase())) &&

                                    <div>
                                        {!this.props.isMobileDevice && index === 0 && this.props.filter === "" ?
                                            <div className="mt-4" style={{
                                                width: '100%',
                                                border: 'none',
                                                overflow:'hidden'
                                            }} /*className={index===0 ? "mt-4" : "mt-4"}*/ /*style={{height:index===0 ? '' : '10rem', display: 'flex', flexDirection: 'column'}}*/>

                                                <div className="row">
                                                    <div ref = {ref => this.mainFigureNode = ref} className="col-md-6">
                                                    <Route render={({history}) => (
                                                        <Figure className="img-fluid" style={{
                                                                height: 'auto',
                                                                maxWidth: '40rem',
                                                                objectFit: 'cover'
                                                            }}>
                                                            <Figure.Image onClick={() => {
                                                                history.push('/article/' + article._id);
                                                                this.renderArticle(article);
                                                                window.scrollTo(0,this.props.offsetTop);
                                                            }} src={article.img_url}/>
                                                        </Figure>
                                                    )}/>
                                                    </div>
                                                    <Route render={({history}) => (
                                                        <div style={{maxWidth:'25rem', marginLeft:'2rem', marginTop:'1rem', height:'auto', fontSize:'2rem', fontFamily:'Helvetica', color: 'white', position: 'absolute'}}>
                                                            <a href="#"
                                                               onClick={(event) => {
                                                                   event.preventDefault();
                                                                   event.stopPropagation();
                                                                   history.push('/article/' + article._id);
                                                                    this.renderArticle(article);
                                                                    window.scrollTo(0,this.props.offsetTop);
                                                                }}
                                                               className="text-left" style={{
                                                                fontSize:'2.2rem',
                                                                lineHeight:'1.8rem',
                                                                color:'white',
                                                                display: 'inline',
                                                                background: 'rgba(0,0,0,0.5)',
                                                                fontWeight: 'Bold',
                                                            }}>{article.title}
                                                            </a>
                                                        </div>
                                                    )}/>
                                                    <div className="col-md-5 m-3">
                                                        <div className="row justify-content-start">
                                                            <a className="mr-1">
                                                                {months[new Date(article.date).getMonth()]}{" "}{new Date(article.date).getFullYear()}
                                                            </a>
                                                            <div>
                                                                {article.tags.split(",").map((tag, index) => (
                                                                    <a key={index} className="ml-1 mr-1 bg-secondary"
                                                                       href="#" onClick={(event) => {
                                                                        event.preventDefault();
                                                                        event.stopPropagation();
                                                                        this.props.setArticleFilter(tag);
                                                                        window.scrollTo(0,this.props.offsetTop);
                                                                    }}
                                                                       style={{
                                                                           margin: 'auto',
                                                                           color: 'white',
                                                                           paddingLeft: "1rem",
                                                                           paddingRight: "1rem"
                                                                       }}
                                                                    >
                                                                        {tag}
                                                                    </a>
                                                                ))}
                                                            </div>
                                                        </div>
                                                        <Card.Text className="row mt-2" style={{
                                                            fontSize: '1.2rem',
                                                            fontFamily: 'Helvetica'
                                                        }}>{article.lead}</Card.Text>
                                                        <div className="row">
                                                            <Link className="text-success" to={"/article/" + article._id} style={{outline:'0'}}
                                                                  onClick={() => {
                                                                      this.renderArticle(article);
                                                                      window.scrollTo(0,this.props.offsetTop);
                                                                  }}>Read
                                                                more</Link>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>


                                            :
                                            <div>
                                            {this.props.isMobileDevice ?
                                                <Card className="mt-4 bg-light" style={{
                                                    width: window.innerWidth + 'px',
                                                    //left: this.props.isMobileDevice ? 0 : '5%',
                                                    border: 'none',
                                                    overflow:'hidden'
                                                }} /*className={index===0 ? "mt-4" : "mt-4"}*/ /*style={{height:index===0 ? '' : '10rem', display: 'flex', flexDirection: 'column'}}*/>

                                                    <Card.Title className="col m-1" style={{
                                                        fontWeight: 'Bold',
                                                        fontSize: "1.6rem",
                                                        fontFamily: 'Helvetica'
                                                    }}>{article.title}</Card.Title>

                                                    <Card.Body className="row">
                                                        <Route render={({history}) => (
                                                            <Card.Img onClick={() => {
                                                                history.push('/article/' + article._id);
                                                                this.renderArticle(article);
                                                                window.scrollTo(0,this.props.offsetTop);
                                                            }} className="img-fluid col-md-4" style={{
                                                                maxHeight: 'auto',
                                                                width: window.innerWidth + 'px',
                                                                objectFit: 'cover'
                                                            }} src={article.img_url}/>
                                                        )}/>
                                                        <div className="col-md-7 m-3">
                                                            <div className="row justify-content-start">
                                                                <a className="mr-1">
                                                                    {months[new Date(article.date).getMonth()]}{" "}{new Date(article.date).getFullYear()}
                                                                </a>
                                                                <div>
                                                                    {article.tags.split(",").map((tag, index) => (
                                                                        <a key={index} className="ml-1 mr-1 bg-secondary"
                                                                           href="#" onClick={(event) => {
                                                                            event.preventDefault();
                                                                            event.stopPropagation();
                                                                            this.props.setArticleFilter(tag);
                                                                            window.scrollTo(0,this.props.offsetTop);
                                                                        }}
                                                                           style={{
                                                                               margin: 'auto',
                                                                               color: 'white',
                                                                               paddingLeft: "1rem",
                                                                               paddingRight: "1rem"
                                                                           }}
                                                                        >
                                                                            {tag}
                                                                        </a>
                                                                    ))}
                                                                </div>
                                                            </div>
                                                            <Card.Text className="row mt-2" style={{
                                                                fontSize: '1.2rem',
                                                                fontFamily: 'Helvetica'
                                                            }}>{article.lead}</Card.Text>
                                                            <div className="row">
                                                                <Link className="text-success" to={"/article/" + article._id} style={{outline:'0'}}
                                                                      onClick={() => {
                                                                          this.renderArticle(article);
                                                                          window.scrollTo(0,this.props.offsetTop);
                                                                      }}>Read
                                                                    more</Link>
                                                            </div>
                                                        </div>
                                                    </Card.Body>
                                                </Card>

                                                :

                                                <div className="mt-4" style={{
                                                width: '90%',
                                                //left: this.props.isMobileDevice ? 0 : '5%',
                                                border: 'none',
                                                overflow:'hidden'
                                            }} /*className={index===0 ? "mt-4" : "mt-4"}*/ /*style={{height:index===0 ? '' : '10rem', display: 'flex', flexDirection: 'column'}}*/>

                                                {/*<Card.Title className="col m-1" style={{
                                                    fontWeight: 'Bold',
                                                    fontSize: "1.6rem",
                                                    fontFamily: 'Helvetica'
                                                }}>{article.title}</Card.Title>
*/}
                                                <div className="row">
                                                    <div className="col-md-4">
                                                        <Route render={({history}) => (
                                                            <Figure
                                                                className="img-fluid"
                                                                style={{
                                                                    maxHeight: '20rem',
                                                                    width: 'auto',
                                                                    objectFit: 'cover'
                                                                }}
                                                            >
                                                                <Figure.Image onClick={() => {
                                                                    history.push('/article/' + article._id);
                                                                    this.renderArticle(article);
                                                                    window.scrollTo(0,this.props.offsetTop);
                                                                    }}

                                                                    src={article.img_url}/>

                                                            </Figure>
                                                        )}/>
                                                    </div>
                                                    <div className="col-md-8">
                                                        <div className="row">
                                                            <Route render={({history}) => (
                                                                <a href="#"
                                                                   onClick={(event) => {
                                                                       event.preventDefault();
                                                                       event.stopPropagation();
                                                                       history.push('/article/' + article._id);
                                                                       this.renderArticle(article);
                                                                       window.scrollTo(0, this.props.offsetTop);
                                                                   }}
                                                                   style={{
                                                                       fontSize: '2rem',
                                                                       color: 'black',
                                                                       fontFamily: 'Helvetica',
                                                                       fontWeight: 'Bold'
                                                                   }}
                                                                >
                                                                    {article.title}
                                                                </a>
                                                            )}/>

                                                            <a className="mr-1">
                                                                {months[new Date(article.date).getMonth()]}{" "}{new Date(article.date).getFullYear()}
                                                            </a>
                                                            <div>
                                                                {article.tags.split(",").map((tag, index) => (
                                                                    <a key={index} className="ml-1 mr-1 bg-secondary"
                                                                       href="#" onClick={(event) => {
                                                                        event.preventDefault();
                                                                        event.stopPropagation();
                                                                        this.props.setArticleFilter(tag);
                                                                        window.scrollTo(0,this.props.offsetTop);
                                                                    }}
                                                                       style={{
                                                                           margin: 'auto',
                                                                           color: 'white',
                                                                           paddingLeft: "1rem",
                                                                           paddingRight: "1rem"
                                                                       }}
                                                                    >
                                                                        {tag}
                                                                    </a>
                                                                ))}
                                                            </div>
                                                        </div>

                                                        <div className="row">
                                                            <Link className="text-success" to={"/article/" + article._id} style={{outline:'0'}}
                                                                  onClick={() => {
                                                                      this.renderArticle(article);
                                                                      window.scrollTo(0,this.props.offsetTop);
                                                                  }}>Read more
                                                            </Link>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            }
                                            </div>

                                        }
                                    </div>
                        }

                    </div>

                ))}
            </Container>
        )
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const auth = state.AuthReducer;
    const conf = state.ConfigReducer;
    return {
        articles: article.articles,
        loading: article.loading_articles,
        filter: article.filter,
        isAuthenticated: auth.isAuthenticated,
        isMobileDevice: conf.isMobileDevice,
        offsetTop: conf.offsetTop,
    }
}

export default connect(mapStateToProps,{
    getArticles,
    gotoArticle,
    setArticleFilter
})(MainPage);