import React, {Component} from "react";
import {
    HashRouter,
    Switch,
    Redirect,
    Route,
    Link,
    useHistory,
} from "react-router-dom";
import MainPage from "./components/MainPage";
import ArticlePage from "./components/ArticlePage";
import ManagementPage from "./components/ManagementPage";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import connect from "react-redux/es/connect/connect";
import {loginUser, logoutUser} from "./actions/authentication";
import {setArticleFilter} from "./actions/article";
import {setMobileDevice, setOffsetTop} from "./actions/configuration";
import Container from "react-bootstrap/Container";
import CreateArticlePage from "./components/CreateArticlePage";
import Card from "react-bootstrap/Card";
import {Row,Col} from "react-bootstrap";
import Collapse from "react-bootstrap/Collapse";
import Figure from "react-bootstrap/Figure";


class Routes extends Component{

    constructor(){
        super();
        this.state = ({
            search: false,
            showLogin: false,
            navExpanded: false,
        });

        this.navbarRef = React.createRef();
        this.searchRef = React.createRef();

        this.onlogin.bind(this);
        this.onlogout.bind(this);
        this.setNavExpanded.bind(this);
        this.closeNav.bind(this);
    }

    componentDidMount(){
        this.props.setOffsetTop(this.navbarRef.current.offsetTop);
        this.props.setMobileDevice(window.innerWidth<500);
    }

    setNavExpanded() {
        this.setState({ navExpanded: true});
    }

    closeNav() {
        this.setState({ navExpanded: false });
    }

    onlogin(usr,pw){
        console.log("login...");
        let user={
            name:usr,
            password:pw
        }
        this.props.loginUser(user)
    }

    onlogout(){
        console.log("logout...");
        this.props.logoutUser();
    }

    handleSubmit(event){
        event.preventDefault();
        event.stopPropagation();
        if(this.props.isAuthenticated){
            this.onlogout();
        }else{
            this.onlogin(event.currentTarget.elements[0].value,event.currentTarget.elements[1].value)
        }
    }

    renderAbout(){

        return(
            <Container ref={this.aboutRef} className="bg-white pt-2">
            <div>
                <div style={{display:'flex', flexDirection:'column', alignItems:'center'}}>
                    <div style={{marginTop: this.props.isMobileDevice ? 0 : '2rem', height:'auto', fontSize:'2rem', fontFamily:'Helvetica', color: 'white', position: 'absolute'}}>
                        <a style={{fontSize:'3rem', display: 'inline', background: 'rgba(0,0,0,0.5)', fontWeight:'Bold'}}>About <br/> Finland China News</a>
                    </div>

                    <Figure style={{margin: this.props.isMobileDevice ? 0 : '2rem', flex:1,height:'auto', width: this.props.isMobileDevice ? window.innerWidth+'px' : '50%'}}>
                        <Figure.Image
                        src={this.props.isMobileDevice ? "https://defaultwebresources.blob.core.windows.net/resources/china-4456833_640.jpg" : "https://defaultwebresources.blob.core.windows.net/resources/china-4456833_1920.jpg"}
                        />
                    </Figure>
                </div>
                <div style={{fontfamily:'Helvetica', marginLeft: (this.props.isMobileDevice ? window.innerWidth*0.0005+'rem' : '25%'), marginRight: (this.props.isMobileDevice ? eval(window.innerWidth*0.0005)+'rem' : '25%')}}>
                    Finland China News was established with the goal to build and expand the Finland China business community. The aim for this platform is to help companies and people make better decisions through access to highly relevant information.
                    <br/>
                    <br/>
                    Today, there are around 400 Finnish companies in China, and around 100 Chinese companies in Finland. Our vision is to become the leading knowledge platform for accessing information that affect the companies, investors and people in this market.
                </div>
            </div>
            </Container>
        );
    }

    renderContact(){
        return(
            <Container ref={this.contactRef} className="fluid pt-5" style={{maxWidth:"50rem",border:"none"}}>
                <Card>
                    <Card.Header className="text-center" style={{fontFamily:'Tw Cen MT', fontSize:"1.5rem"}}>Contact Information</Card.Header>
                    <Row>
                    <Col style={{maxWidth:"10%"}}>
                            <div className="bg-success" style={{height:"100%",width:"1rem"}}></div>
                    </Col>
                        <Col className="col-xs-16 col-sm-16 col-md-16">
                        <Card.Body>
                            <Card.Text style={{fontFamily:'Verdana'}}>
                                Want to get involved, or suggest a good piece of news? We would love to hear your feedback:
                                <br/>
                                <br/>
                                <a href="mailto:finlandchinanews@outlook.com">finlandchinanews@outlook.com</a>
                            </Card.Text>
                        </Card.Body>
                    </Col>
                    </Row>
                </Card>
            </Container>
        );
    }

    render() {
        return (

            <HashRouter basename="/">
                <div className="logo" style={{textAlign:"center", backgroundPosition:"center", height:"15rem", backgroundImage: this.props.isMobileDevice ? `url(https://defaultwebresources.blob.core.windows.net/resources/road-4564817_640.jpg)` : `url(https://defaultwebresources.blob.core.windows.net/resources/road-4564817_1920.jpg)`}}>
                    <h1 style={{paddingTop:"3rem", textAlign: "center", fontFamily: "Tw Cen MT", color:"white", fontSize:'3rem'}}>FINLAND CHINA NEWS<p>   </p></h1>
                </div>
                <Navbar
                    ref={this.navbarRef}
                    onClick={() => {
                        if(this.state.search){
                            this.setState({
                                search:false
                            });
                    }
                    this.props.setArticleFilter("");
                    this.searchRef.value = "";
                    }}
                    collapseOnSelect
                    className="bg-light"
                    expand="lg"
                    onToggle={() => {
                        this.setState({
                                navExpanded:!this.state.navExpanded,
                            });
                    }}
                    expanded={this.state.navExpanded}
                    id="responsive-navbar-nav"
                    >
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse>
                        <Nav className="justify-content-center" style={{ width: "100%"}}>
                            <Nav.Item onClick={() => this.setState({navExpanded:false})}>
                                <Link to={"/mainpage"} className="nav-link mr-5 ml-5 text-center" style={{outline:'0', fontFamily:'Tw Cen MT', fontSize:'1.5rem'}}>HOME</Link>
                            </Nav.Item>
                            <Nav.Item onClick={() => {
                                window.scrollTo(0, this.props.offsetTop);
                                this.setState({navExpanded:false});
                            }} className="mr-5 ml-5 text-center">
                                <Link className="nav-link" to={"/about"} style={{outline:'0', fontFamily:'Tw Cen MT', fontSize:'1.5rem'}}>ABOUT</Link>
                            </Nav.Item>
                            <Nav.Item  onClick={() => {
                                window.scrollTo(0, this.props.offsetTop);
                                this.setState({navExpanded:false});
                            }} className="mr-5 ml-5 text-center">
                                <Link className="nav-link" to={"/contact"} style={{outline:'0',fontFamily:'Tw Cen MT', fontSize:'1.5rem'}}>CONTACT</Link>
                            </Nav.Item>
                            <Nav.Item  onClick={() => this.setState({navExpanded:false})} className="mr-5 ml-5 text-center">
                                    <Nav.Link onClick={() => this.setState({search:!this.state.search})} style={{outline:'0', fontFamily:'Tw Cen MT', fontSize:'1.5rem'}}>SEARCH</Nav.Link>
                                </Nav.Item>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>

                <Collapse in={this.state.search}>
                    <Container>
                        <Route render={({history}) => (
                            <Form.Control ref={ref => this.searchRef = ref} style={{outline:'0', boxShadow:'none', borderColor:'green'}} className="mt-2 text-center" placeholder="search for article" onChange={(evt) => {
                                this.props.setArticleFilter(evt.target.value.toLowerCase());
                                if(window.location.hash !== '#/mainpage'){
                                    history.push('/mainpage');
                                }
                            }}/>
                        )}/>
                    </Container>
                </Collapse>

                {this.props.filter !== "" && window.location.hash === '#/mainpage' &&
                    <Container style={{display:'flex', flexDirection:'row', marginTop:'1rem'}}>
                            <a  href="#"
                                onClick={(event) => {
                                    event.preventDefault();
                                    event.stopPropagation();
                                    this.props.setArticleFilter("");
                                    this.setState({search:false});
                                    this.searchRef.value = "";
                                }}
                                className="text-secondary"
                                style={{fontSize:'1.2rem', textDecoration:'none', flex:0.1, border:'none', background:'transparent'}}
                            >&#10060;</a>
                            <a style={{fontFamily:'Helvetica', marginLeft:'1rem', fontSize:'1.2rem', flex:5}}>Showing articles with keyword '{this.props.filter}':</a>

                    </Container>
                }

                <Switch>
                    <Route exact path="/">
                        <Redirect to="/mainpage" />
                    </Route>
                    <Route path="/mainpage">
                        <MainPage/>
                    </Route>
                    <Route path="/article">
                        <ArticlePage/>
                    </Route>
                    <Route path="/about">
                        {this.renderAbout()}
                    </Route>
                    <Route path="/contact">
                        {this.renderContact()}
                    </Route>
                    {this.props.isAuthenticated &&
                    <Route path="/management">
                        <ManagementPage/>
                    </Route>
                    }
                    {this.props.isAuthenticated &&
                    <Route path="/create" render={(props) => <CreateArticlePage {...props}/>}/>
                    }
                </Switch>

                <div style={{width:"100%", height:"8rem", textAlign:"center", fontFamily:'Helvetica', fontSize:'1rem'}}>
                    <p style={{marginTop:"5rem"}}>Finland China News 2020</p>
                </div>

                <Collapse in={this.state.showLogin}>
                    <Container style={{position:"relative"}}>
                    <Form onSubmit={this.handleSubmit.bind(this)} className="align-text-bottom">
                        {!this.props.isAuthenticated &&
                            <Row className="justify-content-center row-cols-sm-3" style={{margin:"auto",float:"none"}}>
                                <Col className="col-sm-2">
                                    <Form.Control required style={{paddingRight:"0", marginRight:"0"}} size="sm" placeholder="username"/>
                                </Col>
                                <Col className="col-sm-2">
                                    <Form.Control required size="sm" type="password" placeholder="password"/>
                                </Col>
                                <Col className="col-sm-1">
                                    <Button className="btn-secondary" size="sm" type="submit" >Login</Button>
                                    <a href="#" style={{color:"red", fontSize:"0.7rem"}}>
                                        {this.props.errorMessage}
                                    </a>
                                </Col>
                            </Row>
                        }

                        {this.props.isAuthenticated &&
                        <Row className="justify-content-center row-cols-sm-2" style={{margin:"auto",float:"none",width:"60%"}}>
                            <Col className="col-sm-2">
                                <Route render={({history}) => (
                                <Button className="btn-secondary" size="sm" onClick={() => history.push('/')} type="submit" >Logout</Button>
                                    )}/>
                            </Col>
                            <Col className="col-sm-2">
                                <Route render={({history}) => (
                                    <Button className="btn-secondary" size="sm" onClick={() => history.push('/management')} >Manage</Button>
                                )}/>
                            </Col>
                        </Row>
                        }
                    </Form>
                    </Container>
                </Collapse>

                <div style={{width:"100%", height:"5rem", textAlign:"center"}}>
                <a className="ml-1 mr-1 text-secondary"
                 href="#" onClick={(event) => {
                    event.preventDefault();
                    event.stopPropagation();
                    this.setState({showLogin:!this.state.showLogin});
                }}
                   style={{
                       outline:'0',
                       textDecoration:'none',
                       fontSize:'2rem',
                       fontWeight:'Bold',
                       paddingLeft: "1rem",
                       paddingRight: "1rem"
                   }}
                >
                    ...
                </a>
                </div>
            </HashRouter>

        );
    }
}

function mapStateToProps(state) {
    const articles = state.ArticleReducer;
    const auth = state.AuthReducer;
    const conf = state.ConfigReducer;
    return {
        article_info: articles.article_info,
        filter: articles.filter,
        isAuthenticated: auth.isAuthenticated,
        errorMessage: auth.errorMessage,
        isMobileDevice: conf.isMobileDevice,
        offsetTop: conf.offsetTop,
    }
}

export default connect(mapStateToProps, {
    loginUser,
    logoutUser,
    setArticleFilter,
    setMobileDevice,
    setOffsetTop,
})(Routes);