import React,{Component} from "react";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import {Route} from "react-router-dom";
import Col from "react-bootstrap/Col";
import connect from "react-redux/es/connect/connect";
import {getArticles, gotoArticle, deleteArticle} from "../actions/article";
import {getStatistics, resetStatistics} from '../actions/authentication';
import Card from "react-bootstrap/Card";
import Badge from "react-bootstrap/Badge";

class ManagementPage extends Component{
    constructor(){
        super();
        this.state = ({
            createArticle: false,
            statistics: null,
        });
    }

    componentDidMount(){
        // Make sure to get all - manage
        let _this = this;
        this.props.getArticles(this.props.isAuthenticated);
        this.props.getStatistics((stats) => _this.setState({statistics:stats}));
    }

    render(){
        return(
            <Container style={{padding:"2rem"}}>
                <Row className="m-2">
                    <Col>
                        <a style={{fontWeight:'Bold', fontSize:'1.5rem'}}>Manage articles</a>
                    </Col>
                    <Col>
                        <Route render={({history}) => (
                        <Button size="sm" onClick={() => {
                                history.push({pathname:'/create',state:{edit: false, article:null}});
                                }
                            }
                            >New Article</Button>
                        )}/>
                    </Col>
                </Row>
                    <Container>
                        {this.props.articles.map((article,index) => (

                            <Card key={index} className="mt-1 bg-light" style={{border:'none'}} draggable={true}
                                  /*onDragOver={(evt) => {
                                      evt.preventDefault();
                                      console.log("over")
                                  }}
                                  onDragStart={(evt) => {
                                      evt.dataTransfer.setData("article-id",article._id);
                                      evt.dataTransfer.setData("article-order",index);
                                      console.log("drag");
                                  }}
                                  onDrop={(evt) => {
                                      console.log(article._id);
                                      console.log(evt.dataTransfer.getData("article-id"));
                                  }}*/
                            >
                                <Card.Body>
                                    <Row>
                                <Col md={8}>
                                    <Card.Text style={{fontSize:'1rem'}}>{article.title}</Card.Text>
                                </Col>

                                        <Col md={2}>
                                            {/*<input className="mr-2" id="publish" type="checkbox" disabled={true} defaultChecked={article.published}/>
                                            <label htmlFor="publish">Published</label>*/}
                                            <Badge variant={article.published ? "success" : "dark"}>{article.published ? 'Published' : 'Not Published'}</Badge>
                                        </Col>
                                    <Col md={2}>
                                        <Button size="sm bg-danger" className="m-1" style={{border:'none'}}
                                            onClick={() => {
                                                let _this = this;
                                                this.props.deleteArticle(article._id, () => _this.props.getArticles(this.props.isAuthenticated));
                                            }}
                                        >Delete</Button>
                                        <Route render={({history}) => (
                                        <Button size="sm" className="m-1 bg-secondary" style={{border:'none'}}
                                            onClick={() => {
                                                history.push({pathname:'/create',state:{edit: true, article: article}});

                                                }
                                            }
                                        >Edit</Button>
                                        )}/>
                                    </Col>
                                    </Row>
                                </Card.Body>
                            </Card>
                        ))}
                        {this.state.statistics !== null &&
                        <Container className="mt-3">
                            <a style={{fontWeight:'Bold'}}>Requests/Page visits</a>
                            <Badge variant="secondary" className="m-1">{this.state.statistics.length}</Badge>
                            <br/>
                            <Button size="sm" className="m-1" onClick={() =>{
                                let _this = this;
                                this.props.resetStatistics(() => {_this.setState({statistics:[]})});
                            }}>Reset</Button>
                        </Container>
                        }
                        </Container>


            </Container>
        );
    }
}

function mapStateToProps(state) {
    const article = state.ArticleReducer;
    const auth = state.AuthReducer;
    return {
        articles: article.articles,
        isAuthenticated: auth.isAuthenticated
    }
}

export default connect(mapStateToProps,{
    getArticles,
    gotoArticle,
    deleteArticle,
    getStatistics,
    resetStatistics
})(ManagementPage);