import {
    LOGIN_FAILURE,
    LOGIN_REQUEST,
    LOGIN_SUCCESS,
    LOGOUT_SUCCESS,
    LOGOUT_REQUEST
} from "../actions/types";
const { stringify } = require('querystring');

const endpoint_remote = "https://aqueous-coast-74759.herokuapp.com/";
const endpoint_local = "http://localhost:8080/";
const endpoint = endpoint_remote;

function requestLogin(creds) {
  return {
    type: LOGIN_REQUEST,
    isFetching: true,
    isAuthenticated: false,
    user: creds
  }
}

function receiveLogin(user) {
  return {
    type: LOGIN_SUCCESS,
    isFetching: false,
    isAuthenticated: true,
    id_token: user.id_token
  }
}

function loginError(message) {
  return {
    type: LOGIN_FAILURE,
    isFetching: false,
    isAuthenticated: false,
    errorMessage: message,
  }
}

function requestLogout() {
  return {
    type: LOGOUT_REQUEST,
    isFetching: true,
    isAuthenticated: true
  }
}

function receiveLogout() {
  return {
    type: LOGOUT_SUCCESS,
    isFetching: false,
    isAuthenticated: false
  }
}

export function logoutUser(){
  return dispatch => {
    dispatch(requestLogout())
    localStorage.removeItem('access_token')
    dispatch(receiveLogout())
  }
}

export function loginUser(creds){

    let config = {
        method: 'POST',
        headers: { 'Content-Type':'application/x-www-form-urlencoded'},
        body: stringify({name:creds.name,password:creds.password})
    }
    return (dispatch) => {
        dispatch(requestLogin(creds));
        return fetch(endpoint + 'login', config)
          .then(response =>
            response.json()
                .then(user => ({ user, response }))
                ).then(({ user, response }) =>  {

            // Login not successfull
            if (response.status !== 200) {
                console.log("Login failed");
              return Promise.reject(user)

            // Login successfull
            } else {
                // If login was successful, set the token in local storage
                localStorage.setItem('access_token', user.access_token);
                // Dispatch the success action
                dispatch(receiveLogin(user));
            }
      }).catch(err => dispatch(loginError(err.message)))
    }

}

export function resetStatistics(callback = null){
    let config = {
        method: 'POST',
        headers: {'Content-Type':'application/json', 'access_token':localStorage.getItem('access_token')}
    };

    return () => {
        return fetch(endpoint + 'resetStatistics', config)
          .then(response =>
            response.json()
                .then(message => ({ message, response }))
                ).then(({ message, response }) =>  {

            if (response.status !== 200) {
              return Promise.reject(message)
            } else {
                console.log(message)
                if(callback != null){
                    callback();
                }
            }
      }).catch(err => console.log(err))
    }
}

export function getStatistics(callback = null){

    let config = {
        method: 'GET',
        headers: {'Content-Type':'application/json', 'access_token':localStorage.getItem('access_token')}
    };

    return () => {
        return fetch(endpoint + 'getStatistics', config)
          .then(response =>
            response.json()
                .then(stats => ({ stats, response }))
                ).then(({ stats, response }) =>  {

            if (response.status !== 200) {
              return Promise.reject(stats)
            } else {
                console.log(stats);
                if(callback != null){
                    callback(stats);
                }
            }
      }).catch(err => console.log(err))
    }
}