import {LOADING_ARTICLES, ARTICLE_INFO, ARTICLES_RECEIVED, ARTICLE_FILTER} from "../actions/types";

const INITIAL_STATE = {
    article_info: null,
    loading_articles: false,
    articles: [],
    filter:"",
};

export default (state = INITIAL_STATE, action) => {
    switch(action.type){
        case LOADING_ARTICLES:
            return {...state, loading_articles: action.loading_articles}
        case ARTICLE_INFO:
            return  {...state, article_info: action.payload, loading_articles: action.loading_articles};
        case ARTICLES_RECEIVED:
            return {...state, articles: action.articles, loading_articles: action.loading_articles};
        case ARTICLE_FILTER:
            return {...state, filter: action.payload};
        default:
            return state;
    }
};
