import {LOADING_ARTICLES, ARTICLES_RECEIVED, ARTICLE_INFO, ARTICLE_FILTER} from "../actions/types";

const endpoint_remote = "https://aqueous-coast-74759.herokuapp.com/";
const endpoint_local = "http://localhost:8080/";
const endpoint = endpoint_remote;

function requestingArticles(){
    return {
        type: LOADING_ARTICLES,
        loading_articles: true,
    }
}

function receiveArticles(_articles){
  return {
    type: ARTICLES_RECEIVED,
    articles: _articles,
    loading_articles: false,
  }
}

function setArticleInfo(article){
    return {
        type: ARTICLE_INFO,
        payload: article,
        loading_articles: false,
    }
}

export function gotoArticle(article){

    return (dispatch) => {
        dispatch(setArticleInfo(article));
    }
}

export function setArticleFilter(filter){

    return (dispatch) => {
        dispatch({
            type: ARTICLE_FILTER,
            payload: filter
        })
    }
}

