export function deleteArticle(id, callback = null){
    let config = {
        method: 'DELETE',
        headers: { 'Content-Type':'application/x-www-form-urlencoded','access_token':localStorage.getItem('access_token')},
    }
    return () => {
        return fetch(endpoint + 'deleteArticle/' + id, config)
            .then(response =>
            response.json()
                .then(message =>({message,response}))
            ).then(({message, response}) => {

                    if(response.status != 200){
                        console.log("Failed to delete article");
                        return Promise.reject(message);
                    } else {
                        console.log(message);
                        if(callback!=null){
                            callback();
                        }
                    }
            }).catch(err => console.log(err))
    }
}

export function getArticle(id, callback = null){
    let config = {
        method: 'GET',
        headers: { 'Content-Type':'application/x-www-form-urlencoded'},
    }
    return (dispatch) => {
        dispatch(requestingArticles());
        return fetch(endpoint + 'getArticle/' + id, config)
          .then(response =>
            response.json()
                .then(article => ({ article, response }))
                ).then(({ article, response }) =>  {

                // TODO Fall into response not ok, all is ok atm
            if (response.status !== 200) {
              // If there was a problem, we want to
              // dispatch the error condition
                console.log("Failed to get article "+id);

              //dispatch(loginError(user.message));
              return Promise.reject(article)
            } else {
                // If login was successful, set the token in local storage
                //localStorage.setItem('articles', article); // TODO remember to look after these on app entry
                // Dispatch the success action

                dispatch(setArticleInfo(article))

             // dispatch(receiveLogin(user));
            }
      }).catch(err => console.log(err))
    }
}

export function getArticles(auth = false){

        let config = auth === false ? {
        method: 'GET',
        headers: { 'Content-Type':'application/x-www-form-urlencoded'}
        } : {
            method: 'GET',
            headers: { 'Content-Type':'application/x-www-form-urlencoded','access_token':localStorage.getItem('access_token')}
        }
//`name=${creds.username}&password=${creds.password}`
    return (dispatch) => {
        dispatch(requestingArticles());
        return fetch(endpoint + (auth === false ? 'getArticles' : 'getManageArticles') , config)
          .then(response =>
            response.json()
                .then(articles => ({ articles, response }))
                ).then(({ articles, response }) =>  {

    // TODO Fall into response not ok, all is ok atm
            if (response.status !== 200) {
              // If there was a problem, we want to
              // dispatch the error condition
                console.log("Error getting articles");

              //dispatch(loginError(user.message));
              return Promise.reject(articles)
            } else {
                articles = articles.sort((a,b) => new Date(b.date) - new Date(a.date));
              // If login was successful, set the token in local storage
              //localStorage.setItem('articles', articles); // TODO remember to look after these on app entry
              // Dispatch the success action
                dispatch(receiveArticles(articles));
             // dispatch(receiveLogin(user));
            }
      }).catch(err => console.log(err))
    }

}