import {DEVICE_TYPE, OFFSET_TOP} from "../actions/types";

const INITIAL_STATE = {
    isMobileDevice: false,
    offsetTop:0,
};

export default (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case DEVICE_TYPE:
            return {...state, isMobileDevice: action.isMobileDevice};
        case OFFSET_TOP:
            return {...state, offsetTop: action.offset_top};
        default:
            return state;
    }
}