import {DEVICE_TYPE, OFFSET_TOP} from "../actions/types";

function setMobile(isMobile) {
  return {
    type: DEVICE_TYPE,
    isMobileDevice:isMobile,
  }
}

function setOffset(offset){
    return {
        type: OFFSET_TOP,
        offset_top: offset,
    }
}

export function setMobileDevice(isMobile){
     return (dispatch) => {
         dispatch(setMobile(isMobile));
     }
}

export function setOffsetTop(offset){
    return (dispatch) => {
        dispatch(setOffset(offset));
    }
}